*{
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    margin: 0px;
    padding: 0px;
}
.home{
    margin-bottom: 100px;
}
body{
    background-color: rgb(212, 247, 169);
}
.navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: rgb(0, 106, 199);
    padding: 10px 90px;
    font-size: 20px;
    color: rgb(255, 255, 255);
    position: fixed;
    top: 0px;
    width: 90%;
}
.navbar a{
    display: flex;
    color: rgb(255, 255, 255);
    text-decoration: none;
    align-items: center;
}
.navbar #search{
    font-size: 20px;
    padding: 8px 10px 8px 40px;
    width: 400px;
    outline: none;
    border-radius: 8px;
    border: none;
    background-color: rgb(255, 255, 255);
}
.navbar .left{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.navbar .left *{
    margin-right: 30px;
}
.navbar .right *{
    margin-left: 20px;
}
.navbar .right{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.navbar #searchicon{
    position: absolute;
    color: rgb(76, 76, 76);
    left: 255px;
}

/**** Categories CSS ****/
.categories{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin: 85px 70px 30px;
    background-color: rgb(255, 255, 255);
    padding: 10px;
    border-radius: 10px;
}
.category{
    cursor: pointer;
}
.category img{
    height: 100px;
    border-radius: 8px;
}
.category a{
    color: black;
    text-decoration: none;
    font-size: 20px;
}
.categ-menu{
    position: absolute;
    background-color: white;
    padding: 10px;
    border: 1px solid rgb(60, 60, 60);
    border-radius: 10px;
    display: none;
    
}
.categ-menu div{
    padding: 5px 8px;
}
.categ-menu div:hover{
    background-color: rgb(0, 110, 255);
    color: white;
    border-radius: 10px;
}
.categ-menu div a{
    color: rgb(0, 110, 255);
    text-decoration: none;
}
.categ-menu div a:hover{
    color: white;
}
/**** Section 1 ****/
.section1{
    text-align: start;
    margin: 8px 70px;
}
.section1 .items{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    background-color: rgb(255, 255, 255);
    border-radius: 10px;
    overflow-x: auto;
    text-align: center;
    padding: 20px;
}
.section1 .items .item{
    margin: 0px 15px;
    cursor: pointer;
}
.section1 .items .item img{
    height: 150px;
    width: 150px;
    border-radius: 8px;
}

/**** section2  *****/
.section2{
    display: grid;
    grid-template-columns: auto auto auto;
    margin: 10px 70px;
}
.section2 .sect{
    padding: 10px;
    background-color: rgb(255, 255, 255);
    margin: 0px 10px;
    border-radius: 10px;
}
.section2 .sect .items{
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
}
.section2 .sect .item{
    width: 140px;
    text-align: center;
    border: 1px solid black;
    padding: 10px;
    overflow: hidden;
    margin: 10px;
    border-radius: 10px;
    cursor: pointer;
}
.section2 .sect .item img{
    height: 130px;
    border-radius: 7px;
}
#fashion-head{
    margin: 40px 80px 15px;
}
.head{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 40px 0px 10px;
}
#see-all{
    background-color: blue;
    color: white;
    border-radius: 100%;
}
#cart-details{
    color: black;
    background-color: white;
    min-height: 200px;
    width: 210px;
    position: absolute;
    top: 70px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    border-radius: 10px;
    display: none;
}